const state = {
    caseIds: [],
    caseNames: [],
    casePrices: [],
    caseImages: [],
    error: "",
    caseOpen: false,
    prizes: [],
    colors: [],
    open: false
};
const namespaced = true;
const mutations = {
    setCaseIds(state, caseIds) {
        state.caseIds = caseIds;
    },
    setCaseNames(state, caseNames) {
        state.caseNames = caseNames;
    },
    setCasePrices(state, casePrices) {
        state.casePrices = casePrices;
    },
    setCaseImages(state, caseImages) {
        state.caseImages = caseImages;
    },
    setError(state, error) {
        state.error = error;
    },
    setCaseOpen(state, caseOpen) {
        state.caseOpen = caseOpen;
    },
    setPrizes(state, prizes) {
        state.prizes = prizes;
    },
    setColors(state, colors) {
        state.colors = colors;
    },
    setOpen(state, open) {
        state.open = open;
    }
};
export const cases = {
    namespaced,
    state,
    mutations
};
