import App from '@/App';
export default class OpenCaseEvent {
    parse(data) {
        App.interfaceManager.container.$store.commit('cases/setError', "");
        App.interfaceManager.container.$store.commit('cases/setPrizes', data.prizes);
        App.interfaceManager.container.$store.commit('cases/setColors', data.colors);
        App.interfaceManager.container.$store.commit('cases/setCaseOpen', true);
        setTimeout(() => {
            App.interfaceManager.bus.$emit("startCase");
        }, 250);
    }
}
