import App from '@/App';
export default class CasesListEvent {
    parse(data) {
        App.interfaceManager.container.$store.commit('cases/setCaseIds', data.caseIds);
        App.interfaceManager.container.$store.commit('cases/setCaseNames', data.caseNames);
        App.interfaceManager.container.$store.commit('cases/setCasePrices', data.casePrices);
        App.interfaceManager.container.$store.commit('cases/setCaseImages', data.caseImages);
        App.interfaceManager.container.$store.commit('cases/setOpen', true);
    }
}
